<template>
  <div class="music-details" 
  v-if="musicdetails.length > 0 "
    :class="{ bottom:currentmusic.id?true:false}"
  >
    <span class="iconfont icon-fanhui" @click.stop="$router.back()"></span>
    
    <div class="mark"
      :style="{ backgroundImage: `url(${musicdetail.coverImgUrl})` }"
    ></div>

    <div class="music-details-top">
      <div class="pic">
        <img :src="musicdetail.coverImgUrl" alt="" />
        <span>{{ musicdetail.playCount | numberto }}</span>
      </div>
      <div class="music-details-title">
        <span class="name">{{ musicdetail.name }}</span>
        <span v-if="musicdetail.creator" class="avatarUrl">
          <img :src="musicdetail.creator.avatarUrl" alt="" />
        </span>
        <span class="nickname" v-if="musicdetail.creator">{{
          musicdetail.creator.nickname
        }}</span>
      </div>
    </div>

    <h5>歌曲列表:</h5>
    <music-item 
     :musicitem="showMore"
     @play="$emit('play',$event)"
     :currentmusic="currentmusic"
     :currentstate="currentstate"
    ></music-item>
    <div   
     class="loadmore"
     v-if="showMore.length>0"
     ref="loadmore"
     @click.stop="more">
        {{pages>=pagesAll?'已经到底了...':'点击加载更多...'}}
    </div>

    <h5>精彩评论:</h5>
    <my-comments
    :hotComments="showComments"
    ></my-comments>
    <div   class="loadmore"
     v-if="showMore.length>0"
     ref="loadmore"
     @click.stop="CommentsMore">
        {{commentsPages>=CommentspagesAll?'已经到底了...':'点击加载更多...'}}
    </div>
  </div>
</template>

<script>
import MusicItem from "../components/MusicItem.vue";
import MyComments from '../components/MyComments.vue';
export default {
  components: { MusicItem, MyComments },
  props: {
    id:{},
    currentmusic:{
      type:Object,
      default:()=>{
        return null
      }
    },
    currentstate:{
      type:Boolean,
      default:true
    }
  },
  data() {
    return {
      musicdetail: [],
      musicdetails: [],
      pages: 1,
      commentsPages:1,
      hotComments:[]
    };
  },
  created() {
    this.getMusicDetails();
    this.getImg();
    this.getComments();
    console.log(this.id)
  },
//   触底加载
//   mounted(){
//     window.addEventListener("scroll",this.scrollTop);
//   },
//   beforeRouteLeave(to,from,next){
//      window.removeEventListener("scroll",this.scrollTop);
//      next();
//   },
  methods: {
    async getMusicDetails() {
       await this.axios
          .get(`/playlist/track/all?id=${this.id}`)
          .then((res) => {
            console.log(res);
            this.musicdetails = res.songs.map((item,index) => {
              item.song = {
                album: item.al,
                alias: item.alia,
                artists: item.ar,
                i:(index+1<10)?"0"+(index+1):(index+1)
              };
              return item;
            });
          });
    },
    async getImg() {
      await this.axios.get(`/playlist/detail?id=${this.id}`).then((res) => {
        console.log(res);
        this.musicdetail = res.playlist;
      });
    },
    // 滚动事件
//     scrollTop(){
//         //  滚动距离scrollTop,指的是滚动卷去的距离
//       let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
//         //  加载更多..元素距离最顶部的距离
//       let loadmoreoffsetTop = this.$refs.loadmore.offsetTop;
//         //  获取窗口的内容可视区高度
//       let clientHeight = window.innerHeight;
//     //   console.log('scrollTop滚动卷去的距离=>',scrollTop,"loadmoreoffsetTop元素距离最顶部的距离=>",loadmoreoffsetTop,"clientHeight可视区高度=>",clientHeight)
// // scrollTop滚动卷去的距离=> 1275.199951171875 
// // loadmoreoffsetTop元素距离最顶部的距离=> 1912 
// // clientHeight可视区高度=> 667
//     // 判断触底
//       if(scrollTop + clientHeight >= loadmoreoffsetTop+30){
//            this.pages++;
//       }
//    },
    more(){
        this.pages++;
    },
    // 精彩评论
    getComments(){
       this.axios.get(`/comment/playlist?id=${this.id}`).then((res)=>{
         console.log(res);
         this.hotComments = res.hotComments;
       })
    },
    CommentsMore(){
      this.commentsPages++;
    }
  },
  computed:{
    showMore(){
       let end = this.pages * 10;
       return this.musicdetails.slice(0, end);
     },
    pagesAll(){
       return this.musicdetails.length / 10;
    },
    showComments(){
      let end = this.commentsPages*5;
       return this.hotComments.slice(0, end);
    },
     CommentspagesAll(){
       return this.hotComments.length / 5;
    },
  }
};
</script>

<style lang="scss" scoped>
.music-details {

  &.bottom{
  padding-bottom: 56px;
  }
  position: relative;
  .icon-fanhui {
    position: absolute;
    top: 1px;
    left: 8px;
    display:inline-block;
    font-size: 20px;
    color: white;
    z-index: 3;
  }
  .mark {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height:165px;
    overflow: hidden;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    filter: blur(8px) brightness(0.6);
  }

  .music-details-top {
    padding: 25px 15px;
    display: flex;
    position: relative;
    .pic {
      width: 120px;
      height: 120px;
      position: relative;
      &::after {
        content: "";
        width: 100%;
        height: 18%;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 1;
        background-image: linear-gradient(
          rgba(0, 0, 0, 0.2),
          rgba(0, 0, 0, 0.02),
          rgba(255, 255, 255, 0)
        );
      }
      img {
        display: block;
        width: 100%;
      }
      span {
        position: absolute;
        top: 5px;
        right: 5px;
        font-size: 13px;
        color: white;
        text-shadow: 0px 0px 4px black;
        z-index: 5;
        background-image: url(data:image/svg+xml;base64,);
        background-repeat: no-repeat;
        background-size: 13px 12px;
        background-position: 4px 0px;
        padding-left: 20px;
      }
    }
    .music-details-title {
      width: 65%;
      margin: 5px 8px;
      .name {
        line-height: 20px;
        color: black;
        font-synthesis: 14px;
        display: inline-block;
        color: rgba(255, 255, 255, 0.771);
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
      }
      .avatarUrl {
        display: inline-block;
        width: 25px;
        height: 25px;

        img {
          width: 100%;
          display: block;
          border-radius: 50%;
          margin-top: 8px;
        }
      }
      .nickname {
        font-size: 13px;
        margin-left: 2px;
        color: rgba(255, 255, 255, 0.749);
      }
    }
  }
  h5 {
    width: 100%;
    height: 20px;
    font-size: 16px;
    font-weight: bold;
    line-height: 20px;
    padding-left: 10px;
    color: rgba(15, 4, 4, 0.871);
    box-sizing: border-box;
    margin-bottom: 4px;
  }
  .loadmore {
    text-align: center;
    font-size: 13px;
    height: 30px;
    line-height: 30px;
  }
}
</style>