<template>
  <div class="my-comments">
    <ul>
      <li v-for="item in hotComments" :key="item.commentId">
        <img :src="item.user.avatarUrl" alt="" />
        <div class="right">
          <div class="top">
            <span>{{ item.user.nickname }}</span>
            <div class="title">
              <span>{{ item.likedCount }}</span>
              <span class="iconfont icon-good"
              ></span>
            </div>
          </div>
          <div class="time">{{ item.time | filtersTime }}</div>
          <div class="content">{{ item.content }}</div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    hotComments: Array,
  },
  filters: {
    filtersTime(value) {
      if (!value) return "";
      let d = new Date(value);
      let years = d.getFullYear();
      let months = d.getMonth() + 1;
      let date = d.getDate();
      return years + "年" + months + "月" + date + "日";
    },
  },
};
</script>

<style lang="scss" scoped>
.my-comments {
  ul {
    li {
      display: flex;
      justify-content: space-between;
      margin-left: 12px;
      img {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        margin-right: 10px;
        margin-top: 12px;
      }
      .right {
        flex: 1;
        display: flex;
        flex-direction: column;
        padding: 8px 0px;
        border-bottom: 1px solid rgba(128, 128, 128, 0.2);
        .top {
            display: flex;
            justify-content: space-between;
            height: 24px;
            line-height: 24px;
             color: rgba(0, 0, 0, 0.727);
            .title{
                margin-right: 15px;
                color: rgba(128, 128, 128, 0.774);
                font-size: 14px;
                .icon-good{
                    margin-left: 4px;
                    font-size: 16px;
                }
            }
        }
        .time{
            color: rgba(128, 128, 128, 0.774);
            font-size:13px ;
            height: 20px;
            line-height: 18px;
        }
        .content{
            width: 95%;
            line-height: 24px;
            font-size: 14px;
        }
      }
    }
  }
}
</style>